
import Show2goods from '@/components/Show2goods'
import Menu from '@/components/Menu'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Checklist from 'vux/src/components/checklist/index.vue'


export default {
name: 'cart',
components: {
Show2goods,
Menu,
Checklist,
Swipeout,
SwipeoutItem,
SwipeoutButton,
},
data: function () {
return {
goods: [{}], //商品
guessGoods: [],//可能喜欢商品
isSelectAll:false,
selectCount:0,//已选中总数量
totalPrice:0,//选中商品总价
}
},
mounted: function () {
this.$nextTick(function () {
for (let i = 0; i < 8; i++) {
this.guessGoods.push({
id: i,
name: '车载配件' + i,
originalPrice: 1500,
price: 1058 + Math.floor(Math.random() * 200),
sellCount: Math.floor(Math.random() * 200),
img: '/img/good/' + (i + 1) + '.jpg',
isSelected:false,
buyCount:1,//购买数量
maxNum:200,//单件购买最大数量
})

}

this.goods = this.guessGoods;

})
},
methods: {
toClz() {

this.$router.push({name:'clz'})
},
collectGood(item){

alert("收藏商品成功");

this.deleteGood(item);
},
deleteGood(item){

let index = this.goods.indexOf(item)
if(index>-1){
this.goods.splice(index,1)
}
},
selectAll(){

this.isSelectAll = !this.isSelectAll;
this.goods.forEach((item)=>{
if(this.isSelectAll){
item.isSelected = true;
}else {
item.isSelected = false;
}
})
},

goodDetail(goodId){
this.$router.push({name:'good',query:{goodId:goodId}})
},
toPayPage(){

this.$router.push({name:'payPage'})
},
changeCount(item){
console.log("item",item)
if (item.buyCount || item.buyCount == ''){
item.buyCount = 1
}
}

},
watch: {
goods: {
handler(val, oldVal){

console.log("商品变化",val)
this.selectCount = 0;
this.totalPrice = 0;
let selectKind = 0;
val.forEach((item)=>{
if(item.isSelected){
this.selectCount += parseInt(item.buyCount);
this.totalPrice += parseInt(item.buyCount)*item.price.toFixed(2);
selectKind++;
}
})

this.isSelectAll = this.goods.length == selectKind

},
deep:true
}

}


}

